"use client"

import { useEffect, useState } from "react"
import { useQuery } from "convex/react"
import { getAllOrders } from "@/convex/order"
import Link from "next/link"

export default function OrderConfirmation() {
  const [latestOrder, setLatestOrder] = useState(null)
  const orders = useQuery(getAllOrders) || []

  useEffect(() => {
    if (orders.length > 0) setLatestOrder(orders[0])
  }, [orders])

  if (!latestOrder) return <p>Loading order...</p>

  return (
    <div className="max-w-[1110px] m-auto my-20">
      <h2 className="text-2xl font-bold mb-4">
        Thank you for your order, {latestOrder.customerName}!
      </h2>
      <p className="mb-6">
        Your order ID is <strong>{latestOrder._id}</strong>
      </p>

      <div className="bg-white p-6 rounded-md">
        <h3 className="font-bold mb-2">Order Summary</h3>
        {latestOrder.items.map((item) => (
          <div
            key={item.productId}
            className="flex justify-between border-b py-2"
          >
            <span>
              {item.name} x{item.quantity}
            </span>
            <span>${item.price}</span>
          </div>
        ))}
        <div className="flex justify-between mt-4 font-bold">
          <span>Grand Total:</span>
          <span>${latestOrder.grandTotal}</span>
        </div>
      </div>

      <Link
        href="/"
        className="mt-6 inline-block bg-dark-salmon text-white py-2 px-4 rounded-md"
      >
        Back to Home
      </Link>
    </div>
  )
}
